










































































.entry {
	position: relative;
	display: flex;
	flex-wrap: nowrap;
	.icon,
	.file-name,
	.btn-icon {
		display: flex;
		align-items: center;
		margin: 0.2em 0.2em 0.2em 0;
	}
	.file-name {
		flex: 1;
	}
	.icon,
	.btn-icon {
		padding: 0;
		color: inherit;
		user-select: none;
		background: transparent;
		border: none;
	}
	.btn-icon {
		cursor: pointer;
	}
	.btn-delete {
		display: none;
	}
	&:not(.readonly):hover {
		background: #eee;
		.btn-delete {
			display: flex;
		}
	}
}

.material-icons {
	font-size: 20px;
}

.is-file {
	cursor: auto;
}
.is-folder {
	cursor: pointer;
	user-select: none;
}

.is-deleted {
	color: #f00;
	.file-name {
		text-decoration: line-through;
	}
}
.is-updated,
.is-modified {
	color: #d90;
}
.is-upload-error {
	color: #f00;
}
.is-new {
	color: #0a0;
}
.progress {
	position: absolute;
	bottom: 0;
	width: 100%;
	height: 4px;
	-webkit-appearance: none;
	appearance: none;
	&::-webkit-progress-bar {
		background-color: #aaa;
		border-radius: 100px;
	}
	&::-webkit-progress-value {
		background-color: #0a0;
		border-radius: 100px;
	}
}
