






























































































































































































































































ul {
	padding-left: 1.5rem;
	margin: 0;
	li {
		list-style-type: none;
		border-radius: 2px;
		transition: background-color 0.1s linear 0.1s;
		&.dragover {
			background-color: #ccc;
			transition: background-color 0.1s linear;
		}
	}
}
