















































.wrapper {
	margin: 1.5em 0;
}
label {
	font-size: 0.9em;
	font-weight: 500;
	color: black;
}
.textarea {
	display: block;
	width: 100%;
	padding: 8px;
	font-family: inherit;
	font-size: 1.15em;
	color: #757575;
	resize: vertical;
	border-radius: 2px;

	&:focus {
		border-color: var(--primaryColor);
		border-width: 2px;
		outline: none;
	}
}
span {
	font-size: 0.75em;
}
.invalid {
	color: var(--primaryColor);
}
