

























































































































































































































































































































































.container {
	display: flex;
	max-width: 1000px;
	margin: 1em auto;
}

#content-card {
	flex: 2;
	margin-right: 1em;
}

#preview-card {
	flex: 1;
}

.preview-wrapper {
	display: block;
	width: 100%;
}

h3 {
	margin: 2.5em 0 0.5em 0;
	&:first-of-type {
		margin-top: 0;
	}
}

.subtitle {
	margin: 0;
	font-size: 0.9em;
	font-weight: 500;
}

.button_wrapper {
	display: flex;
	justify-content: space-between;
}
